<template>
    <div>
        <a-spin v-if="isLoading" class="support-loading">
            <a-icon slot="indicator" type="loading" style="font-size: 70px" spin/>
        </a-spin>
        <div :class="{'client-title-pc':isPc, 'client-title-mb':!isPc}" v-if="title.length > 0">{{title}}</div>
        <div v-if="!isLoading" class="article-body" :class="{'article-body-mb':!isPc}" v-html="articleBody">
        </div>
    </div>

</template>

<script>
import $ from 'jquery';

export default {
    name: 'SupportArticleView',
    data() {
        return {
            isPc: this.isPc(),
            articleBody: this.article,
            articleId: 0,
            isLoading: true,
            title: '',
            isLogin: !!localStorage.getItem('token')
        };
    },
    props: {
        article: {}
    },
    watch: {
        article: {
            immediate: true,
            handler(val) {
                if (val) {
                    this.article = val;
                    // if this is not the help, is the user's service
                    if (!this.articleId) {
                        this.articleBody = val;
                        this.articleHandler(val)
                    }
                }
            }
        }
    },
    mounted() {
        if (this.$route.params.articleId !== undefined) {
            this.articleId = this.$route.params.articleId.toString();
            if (this.articleId) {
                let articleArray;
                // if (this.articleId.indexOf("-") > -1) {
                //     articleArray = this.articleId.split("-")
                // }
                if (articleArray != null && articleArray.length > 1) {
                    this.articleId = articleArray[0]
                }
                if (!this.isLogin && this.articleId === '360004851731') {
                    this.$router.push({
                        path: 'account/signin',
                        name: 'signin',
                        params: {
                            articleId: this.$route.params.articleId.toString(),
                        }
                    });
                } else {
                    const that = this;
                    let data = require('../assets/articles/' + this.articleId + '.json')
                    that.title = data['article']['title']
                    that.articleHandler(data['article']['body'])
                }
            }
        }
        // document.getElementsByClassName('article-body')[0].innerHTML = this.article
    },
    methods: {
        articleHandler(data) {
            // replace the resource
            this.articleBody = data.replace(/whaleblue\.zendesk\.com/g, 'zendesk.inforun.work');
            // replase the url for order and users services
            this.articleBody = this.articleBody.replace(/https:\/\/jetstream\.surf\/WHMCS\/cart\.php\?.+language=chinese/g, `${window.location.origin}/order/place`)
            this.articleBody = this.articleBody.replace(/https:\/\/jetstream\.surf\/WHMCS\/clientarea\.php\?language=chinese/g, `${window.location.origin}/my/service`)
            // this.articleBody = this.articleBody.replace(/https:\/\/my\.whaleblue\.io/g, `${window.location.origin}/my/service`)
            this.articleBody = this.articleBody.replace(/https:\/\/jetstream\.surf/g, `${window.location.origin}`)
            this.articleBody = this.articleBody.replace(/https:\/\/jetstream\.xyz/g, `${window.location.origin}`)

            if (this.articleId) {// if user in the help
                this.articleBody = this.articleBody.replace(/(https:\/\/zendesk\.auvpn\.net){1}(.*)articles{1}\//g, '');
            } else {
                let lang = this.$i18n.locale;
                // if user in the dashboard
                this.articleBody = this.articleBody.replace(/(https:\/\/zendesk\.auvpn\.net){1}(.*)articles{1}\//g, '/' + lang + '/help/article/');
            }
            // setting the animation
            setTimeout(function () {
                $('.accordion__item-title')
                    .on('click', function () {
                        let $title = $(this);
                        $title.toggleClass('accordion__item-title--active');
                        $title.parents('.accordion__item')
                            .find('.accordion__item-content')
                            .slideToggle();
                    });
                let HC_SETTINGS = {
                    css: {
                        activeClass: 'is-active',
                        hiddenClass: 'is-hidden'
                    }
                };
                $('.tabs-link')
                    .click(function (e) {
                        e.preventDefault();
                        let $link = $(this);
                        let tabIndex = $link.index();
                        let $tab = $link.parents('.tabs')
                            .find('.tab')
                            .eq(tabIndex);
                        $link.addClass(HC_SETTINGS.css.activeClass)
                            .siblings()
                            .removeClass(HC_SETTINGS.css.activeClass);
                        $tab.removeClass(HC_SETTINGS.css.hiddenClass)
                            .siblings('.tab')
                            .addClass(HC_SETTINGS.css.hiddenClass);
                    });
            }, 1000);
            this.isLoading = false
        }
    }
};
</script>

